function dictionary() {
    window.addEventListener('keyup', key => {
        var id = Date.now()
        var select = window.getSelection()
        var selection = select.toString()
        //console.log(selection.length)
        if ((key.key != options.translateKey && key.key != options.translateKey.toUpperCase()) || key.target.nodeName === 'INPUT' || key.target.nodeName === 'TEXTAREA' || (/^\s+$|^$/).test(selection)) return
        // console.log(key, selection, selection.toString())
        var insert_node = $(`<span class='ep-translate' id=${id} style="color:green;margin:0;padding:0;white-space:normal">
        <span>&nbsp;</span>
        <img src="${icon_url}" style="width:0.9em;height:0.9em;vertical-align:baseline"/>
        <span  class='text'>Translating...</span>
        </span>`)
        if (select.anchorNode.compareDocumentPosition(select.focusNode) == 2 || (select.anchorNode.compareDocumentPosition(select.focusNode) == 0 && select.focusOffset < select.anchorOffset)) {
            var right_node = select.focusNode.splitText(select.focusOffset);
            select.focusNode.parentNode.insertBefore(insert_node[0], right_node);
        } else {
            var right_node = select.anchorNode.splitText(select.anchorOffset);
            select.anchorNode.parentNode.insertBefore(insert_node[0], right_node);
        }

        $('.ep-translate').dblclick(event => $(event.target).parents(`.ep-translate[id=${id}]`).remove())
        $(document).keyup(event => { if (event.key == options.clearKey) $('.ep-translate').remove() })
        // console.log(select.anchorNode.compareDocumentPosition(select.focusNode),
        //     select.anchorNode.compareDocumentPosition(select.focusNode) == 2, 'focus', select.focusNode, 'offset', select.focusOffset)
        var first_respond = true; first_respond_error = false
        chrome.runtime.sendMessage({ command: 'dictionary_bing', text: selection, target_lang: options.targetLang },
            function (response) {
                // console.log(response)
                if (response.bing_text != "Error") {
                    if ($(`.ep-translate[id=${id}] .text`).length != 0) { $(`.ep-translate[id=${id}] .text`).remove() }
                    bing_search_url = 'https://cn.bing.com/dict/search?q=' + selection
                    let bing_html = $(`
                    <a href="${bing_search_url}" target="_blank">
                    <img src="${bing_url}" style="width:0.9em;height:0.9em;vertical-align:baseline; cursor:pointer"/></a>
                    <span style="color:green">${response.bing_wo_phonetic}</span>
                    <span></span>`)
                    $(`.ep-translate[id=${id}]`).append(bing_html)
                    //response.bing_wo_phonetic
                } else {
                    if (first_respond == false && first_respond_error == true) {
                        $(`.ep-translate[id=${id}] .text`).text("Error!")
                    }
                    first_respond_error = true
                }
                first_respond = false
            })
        chrome.runtime.sendMessage({ command: 'dictionary_google', text: selection, target_lang: options.targetLang },
            function (response) {
                // console.log(response)
                if (response.google_text != "Error") {
                    if ($(`.ep-translate[id=${id}] .text`).length != 0) { $(`.ep-translate[id=${id}] .text`).remove() }
                    google_search_url = 'https://translate.google.cn/#view=home&op=translate&sl=auto&tl=zh-CN&text=' + encodeURIComponent(selection)
                    let google_html = $(`
                    <a href="${google_search_url}" target="_blank">
                    <img src="${google_url}" style="width:0.9em;height:0.9em;vertical-align:baseline; cursor:pointer"/></a>
                    <span style="color:green">${response.google_text}</span>
                    <span></span>`)
                    $(`.ep-translate[id=${id}]`).append(google_html)
                } else {
                    if (first_respond == false && first_respond_error == true) {
                        $(`.ep-translate[id=${id}] .text`).text("Error!")
                    }
                    first_respond_error = true
                }
                first_respond = false
            })
    })
    window.addEventListener('keyup', key => {
        if (key.key == options.clearKey || key.key == options.clearKey.toUpperCase()) {
            $('.ep-translate').remove()
        }
    })
}

function translate_title(new_pmids, new_bibs) {
    for (var i = 0; i < new_pmids.length; i++) {
        $(`a.docsum-title[href='/${new_pmids[i]}/']`).css('display', 'inline')
        $(`a.docsum-title[href='/${new_pmids[i]}/']`).after(
            `<img src="${title_url}" class="title-translate" id=${new_pmids[i]} style="width:0.9em;height:0.9em;vertical-align:baseline;cursor:pointer"/>`
        )
    }
    $('img.title-translate').click(function () {
        let pmid = $(this).attr('id')
        let title_string = new_bibs[pmid]['Title_get']
        google_search_url = 'https://translate.google.cn/#view=home&op=translate&sl=auto&tl=zh-CN&text=' + encodeURIComponent(title_string)
        $(`a.docsum-title[href='/${pmid}/']`).before(`<span class="ep-translate docsum-title" id=${pmid} style="color:green">
                <img src="${icon_url}" style="width:0.9em;height:0.9em;vertical-align:baseline"/>
                <span class="ep-translate-text" id=${pmid}>Translating...</span>
                </span>`)
        $(`img.title-translate[id=${pmid}]`).hide()
        $(`.ep-translate[id=${pmid}]`).dblclick(event => {
            $(event.target).parents(`.ep-translate`).remove();
            $(`img.title-translate[id=${pmid}]`).show()
        })
        $(document).keyup(event => {
            if (event.key == options.clearKey || event.key.toLowerCase() == options.clearKey) {
                $('.ep-translate').remove();
                $(`img.title-translate`).show()
            }
        })
        if ($(`.ep-translate-text[id=${pmid}]`).text() == 'Translating...') {
            chrome.runtime.sendMessage({ command: 'dictionary_google', text: title_string, target_lang: options.targetLang },
                function (response) {
                    //console.log(pmid, title_string, response.google_text)
                    $(`.ep-translate-text[id=${pmid}]`).before(`<a href="${google_search_url}" target="_blank">
                    <img src="${google_url}" style="width:0.9em;height:0.9em;vertical-align:baseline; cursor:pointer"/></a>`)
                    $(`.ep-translate-text[id=${pmid}]`).text(response.google_text)
                })
        }
    })
}

