function get_scihub_pdf(pmids, bibs) {
    //analyse sci_hub_url and extract the corresponding pdf_url, activate the pdf link
    for (var i = 0; i < pmids.length; i++) {
        if (bibs[pmids[i]]["doi_get"] != "") {
            chrome.runtime.sendMessage({ command: 'analyse_scihub', scihubURL: options.selectedURL, pmid: pmids[i], doi: bibs[pmids[i]]["doi_get"] },
                function (response) {
                    if (response.pdf_url != "") {
                        $(".pdf[id=" + response.pmid + "]").attr("href", response.pdf_url).text("PDF(Full Text)").css('color', '#20558a')
                    } else {
                        $(".pdf[id=" + response.pmid + "]").text("PDF(NOT FOUND)")
                    }
                })
        } else {
            $(".pdf[id=" + pmids[i] + "]").text("PDF(NOT FOUND)")
        }
    }
}

function get_jcr_sci() {
    //load jcr_sci.xml file
    let start = Date.now()
    var ret
    $.ajax({
        type: 'GET',
        url: jcr_sci_url,
        dataType: 'JSON',
        tryCount: 0,
        retryLimit: 3,
        async: false,
        success: function (s) {
            ret = s
        },
        error: function (xhr, textStatus, errorThrown) {
            this.tryCount++;
            if (this.tryCount <= this.retryLimit) {
                //try again
                $.ajax(this);
                return;
            };
            pubmedxml = 0;
            return
        }
    })
    return ret
}

function retry(fn, retries = 3, err = null, i = 1) {
    if (i > retries) {
        return Promise.reject(err);
    }
    return fn().catch(err => {
        return retry(fn, retries, err, i + 1);
    });
}

function get_pubmedxml(pmid, timeout = 5000) {
    /*get_pubmedxml support obtaning multiple pmid each time
Use ajax with 3 try times at most
Return a xml file with multiple aritcle data as a whole*/
    let pubmedxml
    let pmids
    if (pmid.length == 1) {
        pmids = pmid
    } else {
        pmids = pmid.join(",")
    }
    let efetch = "https://eutils.ncbi.nlm.nih.gov/entrez/eutils/efetch.fcgi?db=pubmed&id=" + pmids + '&rettype=abstract';
    return $.ajax({
        type: 'GET',
        url: efetch,
        dataType: 'XML',
        timeout: timeout
    })
}

function analyse_pmbmedxml(pmid, pubmedxml) {
    /*analyse pmid with pubmedxml one in each time and return bib and bibtex format
bib is a named array with Author in x*2 dimension with lastname[0] and firstname[1]
bibtex is bibtex formatted data
*/
    var bib = {}
    var bibtex = ''
    bib['PMID_analyse'] = $("MedlineCitation PMID", pubmedxml);
    bib['Title'] = $("MedlineCitation Article ArticleTitle", pubmedxml);
    bib['Journal'] = $("MedlineCitation MedlineJournalInfo MedlineTA", pubmedxml) //,MedlineCitation Article Journal ISOAbbreviation
    bib['Journal_full'] = $("MedlineCitation Article Journal Title", pubmedxml)
    bib['Abstract'] = $("MedlineCitation Article Abstract AbstractText", pubmedxml)
    bib['Volume'] = $("MedlineCitation Article Journal JournalIssue Volume", pubmedxml)
    bib['Number'] = $("MedlineCitation Article Journal JournalIssue Issue", pubmedxml)
    bib['Year'] = $("MedlineCitation Article Journal JournalIssue PubDate Year, MedlineCitation Article Journal JournalIssue PubDate MedlineDate", pubmedxml)
    bib['Month'] = $("MedlineCitation Article Journal JournalIssue PubDate Month", pubmedxml)
    bib['doi'] = $("MedlineCitation Article ELocationID[EIdType='doi'],ArticleIdList ArticleId[IdType='doi']", pubmedxml)
    bib['Pages'] = $("MedlineCitation Article Pagination MedlinePgn", pubmedxml)
    for (var key in bib) {
        if (bib[key].length == 0) {
            bib[key] = ""
        } else {
            if (key == "Abstract" && bib["Abstract"].length > 1) {
                let Abstract_array = []
                for (var i = 0; i < bib["Abstract"].length; i++) {
                    let label = $(bib["Abstract"][i]).attr("Label")
                    Abstract_array.push(`${label}: ${bib["Abstract"][i].textContent}`)
                    //let label = $(bib["Abstract"][i]).attr("Label").toLowerCase()
                    // Abstract_array.push(`${label.replace(/^./, label[0].toUpperCase())}: ${bib["Abstract"][i].textContent}`)
                }
                bib["Abstract"] = Abstract_array.join("\n")
            } else {
                bib[key] = bib[key][0].textContent
            }
        }
    }

    let names = []
    if ($("Author", pubmedxml).length > 0) {
        var i
        for (i = 0; i < $("Author", pubmedxml).length; i++) {
            var lastname, firstname
            var name = {}
            lastname = $("LastName", $("Author", pubmedxml)[i])
            firstname = $("ForeName", $("Author", pubmedxml)[i])
            if (lastname.length == 1) {
                name["lastname"] = lastname[0].textContent
            } else {
                name["lastname"] = ""
            }
            if (firstname.length == 1) {
                name["firstname"] = firstname[0].textContent
            } else {
                name["firstname"] = ""
            }
            names[i] = name
        }
    }
    if (names == undefined || names.length == 0 || names[0] == undefined) {
        names = [{ lastname: "", firstname: "" }]
    }
    bib["Author"] = names
    bib['PMID'] = pmid
    let efetch = "https://eutils.ncbi.nlm.nih.gov/entrez/eutils/efetch.fcgi?db=pubmed&id=" + pmid + '&rettype=abstract';
    bib["efetch"] = efetch
    bib['filename'] = `PMID${bib['PMID_analyse']}_${bib['Author'][0]["lastname"]}_${bib['Year']}`
    bibtex += `@Article{PMID${bib['PMID_analyse']}_${bib['Author'][0]["lastname"]}_${bib['Year']},\n`
    for (key of ['Title', 'Author', 'Journal', 'Year', 'Volume', 'Number', 'Pages', 'doi']) {
        if (key == "Author") {
            bibtex += 'Author={' + bib['Author'].map(e => Object.values(e).join(', ')).join(' AND ') + "},\n"
            continue
        }
        if (bib[key] != "") {
            bibtex += `${key}={${bib[key]}},\n`
        }
    }
    bibtex += "}"
    bib["bibtex"] = bibtex
    return { "bib": bib }
}

function get_pmid_imfq(pmid, bib, jcr_sci) {
    //get imfq if cant find both, both return as -1
    let temp
    let imfq = {}
    imfq["pmid_imfq"] = pmid
    imfq["imf"] = "NA"
    imfq["q"] = "NA"
    temp = jcr_sci.filter(function (x) { return x.journal == bib["Journal_get"] })
    if (temp.length != 0) {
        imfq["imf"] = temp[0].IF;
        imfq["jcr"] = temp[0].Q;
        imfq["cas"] = temp[0].B;
        imfq["top"] = temp[0].T;
        if (q_cat == 'jcr') {
            imfq["q"] = temp[0].Q;
        } else {
            imfq["q"] = temp[0].B
        }
    }
    return imfq
}

function core_analyse_pubmed(bibs, pubmedxmls) {
    //input pmids  return bibs with bibs[pmid]=[bib:[imf:,q:,pmid:,...],bibtex]
    var i
    // var bibs={}
    for (i = 0; i < $("PubmedArticle", pubmedxmls).length; i++) {
        // let pubmedxml
        let pubmedxml = $("PubmedArticle", pubmedxmls)[i];
        let pmid = $("MedlineCitation PMID", pubmedxml)[0].textContent;
        let temp = analyse_pmbmedxml(pmid, pubmedxml)
        Object.assign(bibs[pmid], temp['bib'])
    }
    return bibs
}

function core_analyse_imfq(bibs) {
    let start = Date.now();
    let id = Object.keys(bibs)
    var jcr_sci
    jcr_sci = get_jcr_sci()
    for (var i = 0; i < id.length; i++) {
        Object.assign(bibs[id[i]], get_pmid_imfq(id[i], bibs[id[i]], jcr_sci))

    }
    return bibs
}

function analyse_pubmed(new_pmids, new_bibs) {
    let timeout_all = new_pmids.length < 100 ? 5000 : 10000
    retry(() => get_pubmedxml(new_pmids, timeout = timeout_all), retries = 2).then(
        function (pubmedxmls) {
            new_bibs = core_analyse_pubmed(new_bibs, pubmedxmls)
            bibs = { ...bibs, ...new_bibs }
            // console.log("b", bibs)
            for (var i = 0; i < new_pmids.length; i++) {
                let pmid = new_pmids[i]
                $("p[id=" + pmid + "]").text(bibs[pmid]["bibtex"]);
                $(".bibtex-button[id=" + pmid + "]").text("View BibTex").css('color', '#20558a').css('cursor', 'pointer')
                display_abstract(pmid)
            }
            renew_listener()
        }).catch(function () {
            analyse_pubmed_single(new_pmids, new_bibs)
            // fail_prompt("Fail connect to bibtex server. Err:" + err)
            // for (var i = 0; i < new_pmids.length; i++) {
            //     let pmid = new_pmids[i]
            //     $(".bibtex-button[id=" + pmid + "]").text("Failed to get BibTex")
            // }
        })
}

function analyse_pubmed_single(new_pmids, new_bibs) {
    // console.log(new_pmids, new_bibs)
    let size = new_pmids.length
    // console.log(new_pmids, new_pmids.length)
    let next = new_pmids
    let count = {}
    let completeCases = []
    for (var i of new_pmids) {
        count[i] = 0
    }
    var interval = setInterval(function (pmid = next[0]) {
        // console.log('completecases', completeCases, size)

        if (completeCases.length == size) {
            // console.log('clear interval', completeCases, new_bibs)
            clearInterval(interval)
            return
        }
        if (pmid == undefined) return
        next.shift()
        get_pubmedxml([pmid], timeout = 60000)
            .done(function (pubmedxml) {
                // console.log('done', "xml", pmid, pubmedxml)
                // console.log("next", next)
                completeCases.push(pmid)
                new_bibs = core_analyse_pubmed(new_bibs, pubmedxml)
                bibs = { ...bibs, ...new_bibs }
                $("p[id=" + pmid + "]").text(bibs[pmid]["bibtex"]);
                $(".bibtex-button[id=" + pmid + "]").text("View BibTex").css('color', '#20558a').css('cursor', 'pointer')
                display_abstract(pmid)
                next = next.filter(i => { return i != pmid })
                renew_listener()
            })
            .fail(function (err) {
                if (count[pmid] > 2) {
                    // console.log('error', 'count', pmid, count[pmid])
                    // fail_prompt("Fail connect to bibtex server. Err:" + err)
                    $(".bibtex-button[id=" + pmid + "]").text("Failed to get BibTex")
                    completeCases.push(pmid)
                    return
                }
                next.push(pmid)
                count[pmid] += 1
                // console.log('error', 'count', pmid, count[pmid])
                // console.log('next', next)
            })
    }, 1000)

}

function display_abstract(pmid) {
    if (bibs[pmid]['Abstract'].length == 0) return
    $(`.search-results-chunk[data-page-number=${bibs[pmid]["search_page"]}] article[data-rel-pos=${bibs[pmid]["pos"]}]`)
        .find(`.full-view-snippet`)
        .append(`<img class='expand' id=${pmid} src='${expand_url}' style="width:12px;height:12px;cursor:pointer"/>`);
    $(`.search-results-chunk[data-page-number=${bibs[pmid]["search_page"]}] article[data-rel-pos=${bibs[pmid]["pos"]}]`)
        .find(`.docsum-snippet`)
        .append(`<div id=${pmid} class="full-view-snippet" style="display:none">
        <span class="abstract-text" id=${pmid} style="white-space: pre-line; font-size:${options.abstractFontSize}">${bibs[pmid]['Abstract']}</span>
    <img class='collapse' id=${pmid} src='${collapse_url}' style="width:12px;height:12px;cursor:pointer"/>
    </div>`)
    if (options.translate == true && options.titleTranslate == true && (location.href.match(/https:\/\/pubmed.ncbi.nlm.nih.gov\/\?term=/) ||
        location.href.startsWith(`https://pubmed.ncbi.nlm.nih.gov/?linkname=pubmed_pubmed_citedin&from_uid=`))) {
        $(`.abstract-text[id=${pmid}]`).before(`<img src="${title_url}" class="abstract-translate-icon" id=${pmid} style="width:0.9em;height:0.9em;vertical-align:baseline;cursor:pointer; margin-right:2px"/>`)
        $(`.abstract-translate-icon[id=${pmid}]`).click(function () {
            let abstract_string = bibs[pmid]['Abstract']
            google_search_url = 'https://translate.google.cn/#view=home&op=translate&sl=auto&tl=zh-CN&text=' + encodeURIComponent(abstract_string)
            $(`.abstract-text[id=${pmid}]`).before(`<p class="ep-translate ep-abstract-translate" id=${pmid} style="color:green; font-size:${options.abstractFontSize}">
                <img src="${icon_url}" style="width:0.9em;height:0.9em;vertical-align:baseline"/>
                <span class="ep-translate-abstract-text" id=${pmid}>Translating...</p>
                </span>`)
            $(`img.abstract-translate-icon[id=${pmid}]`).hide()
            $(`.ep-abstract-translate[id=${pmid}]`).dblclick(event => {
                $(event.target).parents(`.ep-abstract-translate`).remove();
                if ($(`.ep-abstract-translate[id=${pmid}]`).length == 0) { $(`img.abstract-translate-icon[id=${pmid}]`).show() }
            })
            $(document).keyup(event => {
                if (event.key == options.clearKey || event.key.toLowerCase() == options.clearKey) {
                    $('.ep-translate').remove();
                    $(`img.abstract-translate-icon`).show()
                }
            })
            if ($(`.ep-translate-abstract-text[id=${pmid}]`).text() == 'Translating...') {
                chrome.runtime.sendMessage({ command: 'dictionary_google', text: abstract_string, target_lang: options.targetLang },
                    function (response) {
                        //console.log(pmid, abstract_string, response.google_text)
                        $(`.ep-translate-abstract-text[id=${pmid}]`).before(`<a href="${google_search_url}" target="_blank">
                        <img src="${google_url}" style="width:0.9em;height:0.9em;vertical-align:baseline; cursor:pointer"/></a>`)
                        $(`.ep-translate-abstract-text[id=${pmid}]`).text(response.google_text)
                    })
            }
        })
    }
    $(`.expand[id=${pmid}]`).click(function () {
        $(`.search-results-chunk[data-page-number=${bibs[pmid]["search_page"]}] article[data-rel-pos=${bibs[pmid]["pos"]}]`)
            .find(`.full-view-snippet`).hide()
        $(`.full-view-snippet[id=${pmid}]`).show()
    })
    $(`.collapse[id=${pmid}]`).click(function () {
        $(`.search-results-chunk[data-page-number=${bibs[pmid]["search_page"]}] article[data-rel-pos=${bibs[pmid]["pos"]}]`)
            .find(`.full-view-snippet`).show()
        $(`.full-view-snippet[id=${pmid}]`).hide()
    })

}

function create_insert_html(pmid, bib) {
    if (bib == undefined) {
        return ""
    } else {
        var html = "<style>@import url('" + style_url + "')</style>"
        html += "<div class='ep-impactfactor-container' id=" + pmid + ">\
            "
        html += "<dd style='margin-left:30px'>\
                <a style='color:#20558a !important;margin-top: 7.5px' title='Easy Pubmed' href='https://github.com/naivenaive/EasyPubMed'>\
                <img src='"+ chrome.extension.getURL("images/ep_25.png") + "'></a>\
            </dd>";
        switch (bib["q"]) {
            case "B1":
            case "Q1":
                html += "<dd style='background-color:rgba(255, 0, 0, 0.75) !important'>" + bib["q"] + "</dd>"; break;
            case "B2":
            case "Q2":
                html += "<dd style='background-color:rgba(255, 255, 0, 0.8) !important'>" + bib["q"] + "</dd>"; break;
            case "B3":
            case "Q3":
                html += "<dd style='background-color:rgba(0, 255, 0, 0.8) !important'>" + bib["q"] + "</dd>"; break;
            case "B4":
            case "Q4":
                html += "<dd style='background-color:rgba(105, 105, 105, 0.5) !important'>" + bib["q"] + "</dd>"; break;
            case "NA":
                html += "<dd style='background-color:#f3f9d2 !important'>" + bib["q"] + "</dd>"; break;
        }
        html += "<dd>IF: " + bib["imf"] + "</dd>";
        html += `<dd>Cited by:&nbsp;
            <span class='cite-loading' id=${pmid} style="color:grey">Loading...</span>
            <a class='cite' id=${pmid} href="https://pubmed.ncbi.nlm.nih.gov/?linkname=pubmed_pubmed_citedin&from_uid=${pmid}"  target="_blank" style="float:right;display:none"></a>
            </dd>`;
        // html += "<dd><a style='color:grey' target='_blank' id='"+pmid+"'>Loading PDF Address...</a></dd>";
        if (bib["doi_get"] != "") {
            html += "<dd><a class='scihub_link', id=" + pmid + " target='_blank' href='" + options.selectedURL + bib["doi_get"] + "'>Sci-Hub Link</a></dd>";
        } else {
            html += "<dd style='color:grey'>Sci-Hub Link</dd>"
        }
        html += '<dd><a class="pdf" id=' + pmid + ' style="color:grey" target="_blank">Loading PDF...</a></dd>'
        html += '<dd class="bibtex-button" id=' + pmid + ' style="color:grey">Loading...</dd>'
        html += '<div class="modal" id=' + pmid + '>\
                <div class="modal-content" id='+ pmid + '>\
                    <span class="close-button" id='+ pmid + '></span>\
                    <h1 style="margin-top:0.5em;margin-bottom:0.25em">BIBTEX</h1>\
                    <hr />\
                    <p style="white-space: pre-line" id='+ pmid + '></p>\
                    <button class="modal-button" id='+ pmid + '>Download BibTex</button>\
                    <button class="modal-button" id='+ pmid + '>Copy to Clipboard</button>\
                </div>\
            </div>';
        html += "</div>";
        return html
    }
}

