function ini_insert_html() {
    function clear() { ['imf', 'jcr', 'cas', 'top', 'sci', 'hicited'].map(x => $(`form#${x}`).hide()) }
    clear();
    $('button[id$=refresh]').hide();
    $('button[id$=deactivate]').hide();
    $(`form#${$('select#filter-select').val()}`).show()

    $('select#filter-select').change(() => { clear(); $(`form#${$('select#filter-select').val()}`).show() })
    $('button[id$=activate]').click(function () {
        var id = $(this).attr('id').split('-')[0]
        //console.log(id)
        $(`button[id$=activate]`).hide()
        $(`button[id$=refresh]`).show()
        $(`button[id$=deactivate]`).show()
        var filter = []
        switch (id) {
            case "imf":
                filter.push($('input#imf-min').val()); filter.push($('input#imf-max').val()); break
            case "jcr":
                $("input:checkbox[name='jcr']:checked").each(function () { filter.push($(this).val()) }); break
            case "cas":
                $("input:checkbox[name='cas']:checked").each(function () { filter.push($(this).val()) }); break
            case "top":
                $("input:checkbox[name='top']:checked").each(function () { filter.push($(this).val()) }); break
            case "sci":
                $("input:checkbox[name='sci']:checked").each(function () { filter.push($(this).val()) }); break
            case "hicited":
                filter.push($('input#hicited').val())
        }
        //console.log(id, filter)
        filter_pubmed_webpage(id, filter)
    });
    $('button[id$=refresh]').click(function () {
        var id = $(this).attr('id').split('-')[0]
        //console.log(id)
        var filter = []
        switch (id) {
            case "imf":
                filter.push($('input#imf-min').val()); filter.push($('input#imf-max').val()); break
            case "jcr":
                $("input:checkbox[name='jcr']:checked").each(function () { filter.push($(this).val()) }); break
            case "cas":
                $("input:checkbox[name='cas']:checked").each(function () { filter.push($(this).val()) }); break
            case "top":
                $("input:checkbox[name='top']:checked").each(function () { filter.push($(this).val()) }); break
            case "sci":
                $("input:checkbox[name='sci']:checked").each(function () { filter.push($(this).val()) }); break
            case "hicited":
                filter.push($('input#hicited').val())
        }
        //console.log(id, filter)
        filter_pubmed_webpage(id, filter)
    });
    $('button[id$=deactivate]').click(function () {
        var id = $(this).attr('id').split('-')[0]
        //console.log(id)
        $(`button[id$=activate]`).show()
        $(`button[id$=refresh]`).hide()
        $(`button[id$=deactivate]`).hide()
        deactive_filter_pubmed_page()
    });

}

function filter_pubmed_webpage(id, filter) {
    filterSave['id'] = id; filterSave['filter'] = filter
    if (id == "imf") {
        for (var [key, value] of Object.entries(bibs)) {
            // console.log(key, value['imf']);
            let imf = value['imf']
            if (imf == undefined || imf == "NA" || parseFloat(imf) < parseFloat(filter[0]) || parseFloat(imf) > parseFloat(filter[1])) {
                toggle_pubmed_article("hide", value)
                //console.log(key, imf, 'hide')
            } else {
                toggle_pubmed_article("show", value)
                //console.log(key, imf, 'show')
            }
        }
    };
    if (id == "jcr") {
        for (var [key, value] of Object.entries(bibs)) {
            // console.log(key, value['imf']);
            let jcr = value['jcr']
            if (jcr == undefined || jcr == "NA" || !filter.includes(jcr)) {
                toggle_pubmed_article("hide", value)
                // $(`.search-results-chunk[data-page-number=${value['search_page']}]`)
                //     .find(`article[data-rel-pos=${value['pos']}]`).hide()
                //console.log(key, jcr, 'hide')
            } else {
                toggle_pubmed_article("show", value)
                // $(`.search-results-chunk[data-page-number=${value['search_page']}]`)
                //     .find(`article[data-rel-pos=${value['pos']}]`).show()
                //console.log(key, jcr, 'show')
            }
        }
    }
    if (id == "cas") {
        for (var [key, value] of Object.entries(bibs)) {
            // console.log(key, value['imf']);
            let cas = value['cas']
            if (cas == undefined || cas == "NA" || !filter.includes(cas)) {
                toggle_pubmed_article("hide", value)
                //console.log(key, cas, 'hide')
            } else {
                toggle_pubmed_article("show", value)
                //console.log(key, cas, 'show')
            }
        }
    }
    if (id == "top") {
        for (var [key, value] of Object.entries(bibs)) {
            // console.log(key, value['imf']);
            let top = value['top']
            if (top == undefined || top == "NA" || !filter.includes(top)) {
                toggle_pubmed_article("hide", value)
                //console.log(key, top, 'hide')
            } else {
                toggle_pubmed_article("show", value)
                //console.log(key, top, 'show')
            }
        }
    }
    if (id == "sci") {
        for (var [key, value] of Object.entries(bibs)) {
            // console.log(key, value['imf']);
            let q = value['q']
            if (filter[0] == 1) {
                q == "NA" ? toggle_pubmed_article("hide", value) : toggle_pubmed_article("show", value)
            }
            if (filter[0] == 0) {
                q == "NA" ? toggle_pubmed_article("show", value) : toggle_pubmed_article("hide", value)
            }
            if (filter.length == 2) {
                toggle_pubmed_article("show", value)
            }
            if (filter.length == 0) {
                toggle_pubmed_article("hide", value)
            }
        }
    }
    if (id == "hicited") {
        for (var [key, value] of Object.entries(bibs)) {
            // console.log(key, value['imf']);
            let cite = value['cite']
            if (cite == undefined || parseFloat(cite) < parseFloat(filter[0])) {
                toggle_pubmed_article("hide", value)
                //console.log(key, imf, 'hide')
            } else {
                toggle_pubmed_article("show", value)
                //console.log(key, imf, 'show')
            }
        }
    };
}

function deactive_filter_pubmed_page() {
    filterSave = { id: "", filter: "" }
    for (var [key, value] of Object.entries(bibs)) {
        toggle_pubmed_article("show", value)
    }
}

function toggle_pubmed_article(action, bib) {
    if (action == "show") {
        $(`.search-results-chunk[data-page-number=${bib['search_page']}]`)
            .find(`article[data-rel-pos=${bib['pos']}]`).show()
    }
    if (action == "hide") {
        $(`.search-results-chunk[data-page-number=${bib['search_page']}]`)
            .find(`article[data-rel-pos=${bib['pos']}]`).hide()
    }
}

function get_cite(pmid) {
    /*get_pubmedxml support obtaning multiple pmid each time
Use ajax with 3 try times at most
Return a xml file with multiple aritcle data as a whole*/
    let pubmedxml
    let pmids
    if (pmid.length == 1) {
        pmids = pmid
    } else {
        pmids = pmid.join("&id=")
    }
    let efetch = "https://eutils.ncbi.nlm.nih.gov/entrez/eutils/elink.fcgi?dbfrom=pubmed&linkname=pubmed_pubmed_citedin&id=" + pmids;
    //console.log("start get cite")
    return $.ajax({
        type: 'GET',
        url: efetch,
        dataType: 'XML',
        timeout: 20000
    })
}

function analyse_cite(pmid) {
    var pmids = Array.from(pmid)
    retry(() => get_cite(pmids)).then(citexml => {
        for (var i of pmids) {
            let cite_no = $(`Id:contains(${i})`, citexml).parent().next().find('Id').length
            $(`.cite-loading[id=${i}]`).hide()
            $(`.cite[id=${i}]`).text(cite_no).show()
            bibs[i]['cite'] = cite_no
        }
        if (filterSave['id'] == 'hicited') {
            filter_pubmed_webpage(filterSave['id'], filterSave['filter'])
        }
        //console.log(bibs)
    }).catch(function (err) {
        fail_prompt("Fail in getting citation number. Err:" + err,)
        for (var i of pmids) {
            $(`.cite-loading[id=${i}]`).text("NA")
        }
    })
}

//relate to background.js
function setItem(name, data) {
    chrome.runtime.sendMessage({ command: 'setItem', name: name, data: data });
}


//modal related function
function openModal(event) {
    modal.filter(function () {
        return $(this).attr("id") === event.target.id
    })[0].classList.toggle("show-modal", true);
}

function toggleModal(event) {
    modal.filter(function () {
        return $(this).attr("id") === event.target.id
    })[0].classList.toggle("show-modal");
}

function windowOnClick(event) {
    if (event.target.className === "modal show-modal") {
        toggleModal(event);
    }
}

function download(filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);
    element.style.display = 'none';
    document.body.appendChild(element);
    element.click();
    document.body.removeChild(element);
}

function downloadOnClick(event) {
    download("bibtex_" + bibs[event.target.id]["filename"] + ".txt", bibs[event.target.id]["bibtex"]);
}

function copytoClip(content) {
    var aux = document.createElement("textarea");
    aux.value = content;
    document.body.appendChild(aux);
    aux.select();
    document.execCommand("copy");
    document.body.removeChild(aux);
    success_prompt("Success copy bibtex to clipboard");
}

function copyOnClick(event) {
    copytoClip(bibs[event.target.id]["bibtex"])
}

var prompt = function (message, style, time) {
    style = (style === undefined) ? 'ep-alert-success' : style;
    time = (time === undefined) ? 2000 : time;
    $('<div>')
        .appendTo('body')
        .addClass('ep-alert ' + style)
        .html(message)
        .show()
        .delay(time)
        .fadeOut();
};

var success_prompt = function (message, time) {
    prompt(message, 'ep-alert-success', time);
};

var fail_prompt = function (message, time) {
    prompt(message, 'ep-alert-danger', time);
};


